<template>
  <div>
    <!-- hero Section Starts -->
    <section id="hero">
      <div class="py-60 xl:px-96 text-center">
        <h2 class="font-heading text-5xl sm:text-4xl lg:text-6xl font-thin">Blog</h2>
      </div>
    </section>

    <!-- blog-card Section Starts -->
    <section id="blog-card">
      <div class="container mx-auto ">
        <div class="columns-1 md:columns-2 lg:columns-3 gap-7 px-5 lg:px-0">

          <div v-for="(item, i) in this.blogData" :key="i" class="blog-post py-10" :style="{ visibility: item.title !== 'Blog PlaceHolder' ? 'visible' : 'hidden' }">
            <div class="image-zoom">
              <a href="blog-single.html" class="blog-img"><img :src="getImageSrc(item.cover)" alt="" class=""></a>
            </div>
            <div class="pt-8">
              <span class="blog-date uppercase">{{ item.date }}</span>
            </div>
            <div class="">
              <h3 class="py-5"><a href="blog-single.html"
                                  class="font-heading font-thin text-2xl hover:text-gray-500">
                {{ item.title }}
              </a></h3>
              <p class="pb-10">{{ sliceString(item.content) }}</p>
              <a href="blog-single.html"
                 class="font-heading text-sm font-normal py-4 px-8 bg-transparent hover:bg-black text-black hover:text-white border-black border-2 hover:border-transparent rounded-full transition duration-700 ease-in-out">
                Read More
              </a>
            </div>
          </div>

        </div>


        <div class="flex justify-center mt-10">
          <!--          <div class="px-3 pt-1"> <a href="#" class="blog-swipe"><iconify-icon class="blog-icon" icon="ic:round-arrow-back"></iconify-icon></a> </div>-->

          <div v-for="i in pageSize" :key="i" class="px-3">
            <router-link :to="{ name: 'Blog', query: {page: i} }" class="blog-swipe">{{ i }}</router-link>
          </div>
          <!--          <div class="px-3 pt-1"> <a href="#" class="blog-swipe"> <iconify-icon class="blog-icon" icon="ic:round-arrow-forward"></iconify-icon></a></div>-->
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import {blogData, placeholder} from "../data/blog";

export default {
  name: "Blog",
  data() {
    return {
      blogData: [],
      pageSize: 1,
      currentBreakpoint: '',
      page: 1
    }
  },
  watch: {
    '$route.query.page': {
      handler(newPage, oldPage) {
        if (newPage !== oldPage) {
          this.getPage();
        }
      },
      immediate: true,
    },
  }, mounted() {
    window.addEventListener('resize', this.updateBreakpoint);
  }, beforeDestroy() {
    window.removeEventListener('resize', this.updateBreakpoint);
  },
  methods: {
    getImageSrc(coverPath) {
      const images = require.context('@/images', false, /\.png$/);
      return images(`./${coverPath.split('/').pop()}`);
    }, updateBreakpoint() {
      const breakpoints = {
        xl: window.matchMedia('(min-width: 1280px)'),
        lg: window.matchMedia('(min-width: 1024px) and (max-width: 1279px)'),
        md: window.matchMedia('(min-width: 768px) and (max-width: 1023px)'),
        sm: window.matchMedia('(max-width: 767px)')
      };
      if (breakpoints.xl.matches) {
        this.currentBreakpoint = 'xl';
      } else if (breakpoints.lg.matches) {
        this.currentBreakpoint = 'lg';
      } else if (breakpoints.md.matches) {
        this.currentBreakpoint = 'md';
      } else if (breakpoints.sm.matches) {
        this.currentBreakpoint = 'sm';
      }
      let start = (this.page - 1) * 12;
      let end = this.page * 12 > blogData().length ? blogData().length : this.page * 12;
      let blogs = blogData().slice(start, end);
      if (this.currentBreakpoint === 'sm') {
        this.blogData = blogs;
      } else if (this.currentBreakpoint === 'md') {
        this.blogData = [];
        for (let i = 0; i < blogs.length; i += 2) {
          this.blogData.push(blogs[i]);
        }
        for (let i = 1; i < blogs.length; i += 2) {
          this.blogData.push(blogs[i]);
        }
        if (this.blogData.length % 2 !== 0) {
          this.blogData.push(placeholder());
        }
      } else {
        this.blogData = [];
        for (let i = 0; i < blogs.length; i += 3) {
          this.blogData.push(blogs[i]);
        }
        for (let i = 1; i < blogs.length; i += 3) {
          this.blogData.push(blogs[i]);
        }
        if (this.blogData.length % 2 !== 0) {
          this.blogData.push(placeholder());
        }
        for (let i = 2; i < blogs.length; i += 3) {
          this.blogData.push(blogs[i]);
        }
        if (this.blogData.length % 3 !== 0) {
          this.blogData.push(placeholder());
        }
      }
      console.log(this.blogData)
    }, sliceString(string) {
      let words = string
      if (string.length > 140) {
        let spaceIndex = string.slice(140).indexOf(' ');
        words = string.slice(0, 140 + spaceIndex) + "...";
      }
      return words.replace(/\r?\n/g, ' ');
    }, getPage() {
      this.page = this.$route.query.page;
      this.pageSize = Math.ceil(blogData().length / 12);
      this.updateBreakpoint();
    }
  }
}
</script>

<style scoped lang="scss">


</style>



