<script>

export default {
  name: "Main",
  data() {
    return {
      lastRoute: this.$route.fullPath,
    }
  },
  watch: {
    $route(to, from) {
      if ((this.lastRoute === "/cHome" && to.path === "/cGallery")
        || (to.path === "/cHome" && this.lastRoute === "/cGallery")
        || ((this.lastRoute !== "/cHome" && this.lastRoute !== "/cGallery")) && (to.path === "/cHome" || to.path === "/cGallery")
        || (to.path === "/Home")) {
        window.location.reload();
      }
      if (to.path === "/cHome" || to.path === "/cGallery") {
        this.lastRoute = to.path;
      }
    }
  },
  mounted() {
    let scripts = [
      require("../assets/jquery-1.11.0.min.js"),
      require("../assets/plugins.js"),
      require("../assets/script.js"),
      "https://cdn.jsdelivr.net/npm/iconify-icon@1.0.7/dist/iconify-icon.min.js"
    ];
    scripts.forEach(script => {
      let tag = document.createElement("script");
      tag.setAttribute("src", script);
      window.document.body.appendChild(tag)
    });
  }
}
</script>

