<template>
  <div>
    <!-- hero Section Starts -->
    <section id="hero">
      <div class="py-60 xl:px-96 text-center">
        <h2 class="font-heading text-5xl sm:text-4xl lg:text-6xl font-thin">相册</h2>
      </div>
    </section>

    <section id="gallery">

      <div class="container mx-auto px-5 lg:px-0 ">
        <h2 class=" font-heading text-4xl font-thin mb-7">我的影集</h2>

        <div class="py-5 lg:pt-0">
          <button
            class="filter-button font-heading text-sm rounded-full hover:bg-primary hover:text-white px-4 py-2 mt-3 mr-3 lg:mt-0 active uppercase"
            data-filter="*">显示全部</button>
          <button v-for="(item, i) in tags" :key="i"
                  class="filter-button font-heading text-sm rounded-full hover:bg-primary hover:text-white px-4 py-2 mt-3 mr-3 lg:mt-0"
                  :data-filter="'.' + item">{{ item }}</button>
        </div>

        <div class="isotope-container">
          <div class=" grid grid-cols-4 gap-4">
            <div v-for="(item, i) in galleryData" :key="i" :class="['item', item.cTag]">
              <a :href="getImageSrc(item.cover)" class="image-link">
                <img :src="getImageSrc(item.cover)" class=" post-image object-contain">
              </a>
            </div>

          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import {galleryData} from '../data/gallery.js'

export default {
  name: "Gallery_CN",
  data() {
    return {
      galleryData: galleryData(),
      tags: []
    }
  },
  mounted() {
    this.getTags()
  },
  methods: {
    getImageSrc(coverPath) {
      const images = require.context('@/images', false, /\.png$/);
      return images(`./${coverPath.split('/').pop()}`);
    }, getTags() {
      this.galleryData.forEach(item => {
        if (!this.tags.includes(item.cTag)) {
          this.tags.push(item.cTag)
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">

img {
  object-fit: cover;
  width:360px;
  height:360px;
}

</style>



