function Blog(title, cTitle, content, cContent, date, cDate, cover){
  this.title = title;
  this.cTitle = cTitle;
  this.content = content;
  this.cContent = cContent;
  this.date = date;
  this.cDate = cDate;
  this.cover = cover;
}

export function placeholder() {
  return new Blog("Blog PlaceHolder", "博客占位符", "I am so happy, my dear friend, so absorbed in the exquisite sense of mere tranquil existence, that I neglect my talents. I should be incapable of drawing since", "亲爱的朋友，我是如此快乐，如此沉浸在宁静生活的美妙感觉中，以至于忽略了自己的才能。", "1st Jan 1970", "1970 年 1 月 1 日", "@/images/placeholder.png");
}

