<template>
  <div>
    <!-- hero Section Starts -->
    <section id="hero" class="jarallax jarallax-img title-img"
             :style="{'background-image': `url(${require('@/images/background.png')})`}">
      <div class="py-80 xl:px-96 text-center">
        <h2 class="font-heading text-5xl sm:text-6xl lg:text-8xl font-thin">Watch Life<br>Getting Better</h2>
        <p class="text-xl sm-text-2xl lg:text-4xl xl:px-40 py-10 text-stone-700">Hi, I’m a K-pop & Dota enthusiast with
          10 years of passion, eager to share the trivialities of my life.</p>
      </div>
    </section>

    <!-- blog-card Section Starts -->
    <section id="blog-card">
      <div class="container mx-auto py-28">

        <div class="columns-1 md:columns-2 lg:columns-3 gap-7 px-5 lg:px-0">

          <div v-for="i in 6" :key="i" class="blog-post py-10">
            <div class="image-zoom">
              <a href="blog-single.html" class="blog-img"><img :src="getImageSrc(blogData[i - 1].cover)" alt="" class=""></a>
            </div>
            <div class="pt-8">
              <span class="blog-date uppercase">{{blogData[i - 1].date}}</span>
            </div>
            <div class="">
              <h3 class="py-5"><a href="blog-single.html"
                                  class="font-heading font-thin text-2xl hover:text-gray-500">
                {{blogData[i - 1].title}}
              </a></h3>
              <p class="pb-10">{{sliceString(blogData[i - 1].content)}}</p>
              <a href="blog-single.html"
                 class="font-heading text-sm font-normal py-4 px-8 bg-transparent hover:bg-black text-black hover:text-white border-black border-2 hover:border-transparent rounded-full transition duration-700 ease-in-out">
                Read More
              </a>
            </div>
          </div>

        </div>
      </div>
    </section>

    <!-- Blog-block Section Starts -->
    <section id="blog-block">
      <div class="columns-5 gap-0 pt-40">

        <figure v-for="(item, i) in galleryData" :key="i" class="blog-block-content image-zoom relative">
          <router-link to="/Gallery" class=" ">
            <img class="blog-block-img " :src="getImageSrc(item.cover)" alt="">
            <div class="header-overlay hidden lg:flex absolute inset-x-0 bottom-0 justify-center">
            </div>
          </router-link>
        </figure>

      </div>
    </section>
  </div>
</template>

<script>
import {blogData} from '../data/blog.js';
import {galleryData} from '../data/gallery.js'

export default {
  name: "Home",
  data() {
    return {
      blogData: [],
      galleryData: galleryData().slice(0, 5),
      currentBreakpoint: ''
    }
  },
  created() {
    this.updateBreakpoint();
  }, mounted() {
    window.addEventListener('resize', this.updateBreakpoint);
  }, beforeDestroy() {
    window.removeEventListener('resize', this.updateBreakpoint);
  },
  methods: {
    getImageSrc(coverPath) {
      const images = require.context('@/images', false, /\.png$/);
      return images(`./${coverPath.split('/').pop()}`);
    }, updateBreakpoint() {
      const breakpoints = {
        xl: window.matchMedia('(min-width: 1280px)'),
        lg: window.matchMedia('(min-width: 1024px) and (max-width: 1279px)'),
        md: window.matchMedia('(min-width: 768px) and (max-width: 1023px)'),
        sm: window.matchMedia('(max-width: 767px)')
      };
      let beforeBreakpoint = this.currentBreakpoint;
      if (breakpoints.xl.matches) {
        this.currentBreakpoint = 'xl';
      } else if (breakpoints.lg.matches) {
        this.currentBreakpoint = 'lg';
      } else if (breakpoints.md.matches) {
        this.currentBreakpoint = 'md';
      } else if (breakpoints.sm.matches) {
        this.currentBreakpoint = 'sm';
      }
      if (beforeBreakpoint !== this.currentBreakpoint) {
        let blogs = blogData().slice(0, 6);
        if (this.currentBreakpoint === 'sm') {
          this.blogData = blogs;
        } else if (this.currentBreakpoint === 'md') {
          this.blogData = [blogs[0], blogs[2], blogs[4], blogs[1], blogs[3], blogs[5]];
        } else {
          this.blogData = [blogs[0], blogs[3], blogs[1], blogs[4], blogs[2], blogs[5]];
        }
      }
    }, sliceString(string) {
      let words = string
      if (string.length > 140) {
        let spaceIndex = string.slice(140).indexOf(' ');
        words = string.slice(0, 140 + spaceIndex) + "...";
      }
      return words.replace(/\r?\n/g, ' ');
    }
  }
}
</script>

<style scoped lang="scss">
.blog-block-img {
  object-fit: cover;
  width:360px;
  height:360px;
}

</style>



