import Vue from "vue";
import VueRouter from "vue-router";
import Main from '@/views/Main'
import Main_CN from '@/views/Main_CN'

import Home from "@/views/Home";
import About from "@/views/About";
import Blog from "@/views/Blog";
import Gallery from "@/views/Gallery";
import FAQ from "@/views/FAQ";
import Contact from "@/views/Contact";

import Home_CN from "@/views/Home_CN";
import About_CN from "@/views/About_CN";
import Blog_CN from "@/views/Blog_CN";
import Gallery_CN from "@/views/Gallery_CN";
import FAQ_CN from "@/views/FAQ_CN";
import Contact_CN from "@/views/Contact_CN";

Vue.use(VueRouter);
Vue.use(Home);
Vue.use(About);
Vue.use(Blog);
Vue.use(Gallery);
Vue.use(FAQ);
Vue.use(Contact);
Vue.use(Main);

Vue.use(Home_CN);
Vue.use(About_CN);
Vue.use(Blog_CN);
Vue.use(Gallery_CN);
Vue.use(FAQ_CN);
Vue.use(Contact_CN);
Vue.use(Main_CN);

const router = new VueRouter({
  // mode: 'history',
  routes: [
    {
      path: '/Main',
      component: Main,
      children: [
        {
          path: '/Home',
          name: 'Home',
          component: Home
        },{
          path: '/About',
          name: 'About',
          component: About
        },{
          path: '/Blog',
          name: 'Blog',
          component: Blog
        },{
          path: '/Gallery',
          name: 'Gallery',
          component: Gallery
        },{
          path: '/FAQ',
          name: 'FAQ',
          component: FAQ
        },{
          path: '/Contact',
          name: 'Contact',
          component: Contact
        }
      ],
      redirect: '/Home'
    }, {
      path: '/cMain',
      component: Main_CN,
      children: [
        {
          path: '/cHome',
          name: 'Home_CN',
          component: Home_CN
        },{
          path: '/cAbout',
          name: 'About_CN',
          component: About_CN
        },{
          path: '/cBlog',
          name: 'Blog_CN',
          component: Blog_CN
        },{
          path: '/cGallery',
          name: 'Gallery_CN',
          component: Gallery_CN
        },{
          path: '/cFAQ',
          name: 'FAQ_CN',
          component: FAQ_CN
        },{
          path: '/cContact',
          name: 'Contact_CN',
          component: Contact_CN
        }
      ],
      redirect: '/cHome'
    }, {
      path: '/',
      redirect: '/Home'
    }
  ]
});


export default router



